<template>
<div class="college-full-screen college">
   <div class="college_content">
      <div class="cl-student_head">
            <span @click="goback" class="cl-back">
              <i style="color:#b0f9e0" class="el-icon-back"></i>
               <span>返回列表</span>
            </span>
            <label>{{xuerowifon.name}}跟踪分析报告</label>
      </div>
      <div class="cl-student_info cl-box">
            <h5 class="cl-text">学生信息</h5>
            <ul>
               <li class="cl-text">姓名：{{xuerowifon.name}}</li>
               <li class="cl-text">学号：{{xuerowifon.code}}</li>
               <li class="cl-text">所属学院：{{xuerowifon.academy_name}}</li>
               <li class="cl-text">所属专业：{{xuerowifon.specialty_name}}</li>
               <li class="cl-text">所属班级：{{xuerowifon.class_name}}</li>
               <li class="cl-text">入学年份：{{xuerowifon.grade}}</li>
               <li class="cl-text">培养方案版本：{{xuerowifon.versionName}}</li>
            </ul>
      </div>
      <div class="college_search">
            <div class="college_search_lf">
               <span class="cl-btn">能力达成概况</span>
            </div>
      </div>
      <div class="cl-student_content cl-box">
            <div class="cl-student_content_side">
               <ul>
                  <li @click="tabClick(index, 0,vo)" v-for="(vo, index) in options" :key="index" class="cl-side-text" :class="{active:activeIndex===index }">
                     <span v-if="vo.term == 2">{{vo.grade }}-{{vo.grade+1}}学年 第二学期</span>
                     <span v-if="vo.term == 1">{{vo.grade }}-{{vo.grade+1}}学年 第一学期</span>
                  </li>
               </ul>
            </div>
            <div class="cl-student_content_main">
               <div class="student_content_main_top" v-if="topOptions.length !==0">
                  <ul>
                        <li :class="{topActive:topActiveIndex === index}" @click="topClick(vo, index)" v-for="(vo,index) in topOptions" :key="index" class="cl-side-text">{{vo.name}}</li>
                  </ul>
               </div>
               <div class="bar-box">
                  <p class="title-tips cl-text"><label>毕业要求：</label>{{nameTips}}</p>
                  <echarts5 id="sbilityQueryBar" :series-bar-data="seriesBarData" :isMax="true" :isMin="true" chartHeight="200px" ></echarts5>
               </div>
            </div>
      </div>
      <div class="college_search">
            <div class="college_search_lf">
               <span class="cl-btn">预警记录</span>
            </div>
      </div>
      <div class="cl-student_content cl-box">
            <div class="cl-student_content_side">
               <ul>
                  <li @click="tabClick(index, 1,vo)" v-for="(vo, index) in options" :key="index" class="cl-side-text" :class="{active:activeIndexTwo===index }">
                     <span v-if="vo.term == 2">{{vo.grade }}-{{vo.grade+1}}学年 第二学期</span>
                     <span v-if="vo.term == 1">{{vo.grade }}-{{vo.grade+1}}学年 第一学期</span>
                  </li>
               </ul>
            </div>
            <div class="cl-student_content_main">
               <!-- <div class="student_content_main_top" v-if="topOptions.length !==0"> -->
                  <!-- <ul>
                     <li :class="{topActive:topActiveIndex1 === index}" @click="topClick1(vo, index)" v-for="(vo,index) in topOptions1" :key="index" class="cl-side-text">{{vo.name}}</li>
                  </ul> -->
               <!-- </div> -->
               <h5 class="cl-text">{{gradeifon}}学年第{{tremifon}}学期能力达成情况分析预警</h5>
               <ul class="cl-student-point">
                  <li class="cl-student-point_li" >
                        <div v-if="pointOption.type1" style="overflow: hidden;margin-bottom: 15px;">
                           <div class="cl-student-point_left">
                             <SvgIcon icon-class="icontongguo" v-if="pointOption.type1" size="40px"></SvgIcon>
                           </div>
                           <div class="cl-student-point_right">
                              <p style="font-size:14px;font-family:PingFang SC;font-weight:bold;color:rgba(3,177,135,1);">{{pointOption.type1.split(",").join('， ')}}</p>
                              <span class="cl-text" style="line-height: 20px;" v-if="pointOption.type1">{{pointOption.returnFeedback1.split(",").join('， ')}}</span>
                              <!-- <div class="content">
                                 <ul>
                                       <li :class="{textActive: num===0}" v-for="(item,num) in vo.courses" :key="num" class="cl-side-text">{{item.courseName}}</li>
                                 </ul>
                              </div> -->
                           </div>
                        </div>
                       <div v-if="pointOption.type2" style="overflow: hidden;margin-bottom: 15px;">
                           <div class="cl-student-point_left">
                             <SvgIcon icon-class="iconzhuyi" v-if="pointOption.type2" size="40px"></SvgIcon>
                           </div>
                           <div class="cl-student-point_right">
                              <p style="font-size:14px;font-family:PingFang SC;font-weight:bold;color:rgba(255,143,68,1);">{{pointOption.type2.split(",").join('， ')}}</p>
                              <span class="cl-text" style="line-height: 20px;" v-if="pointOption.type2">{{pointOption.returnFeedback2.split(",").join('， ')}}</span>
                              <!-- <div class="content">
                                 <ul>
                                       <li :class="{textActive: num===0}" v-for="(item,num) in vo.courses" :key="num" class="cl-side-text">{{item.courseName}}</li>
                                 </ul>
                              </div> -->
                           </div>
                        </div>
                        <div v-if="pointOption.type3" style="overflow: hidden;margin-bottom: 15px;">
                           <div class="cl-student-point_left">
                             <SvgIcon icon-class="iconzhuyi-hongse" v-if="pointOption.type3" size="40px"></SvgIcon>
                           </div>
                           <div class="cl-student-point_right">
                              <p style="font-size:14px;font-family:PingFang SC;font-weight:bold;color:rgba(255,94,98,1);">{{pointOption.type3.split(",").join('， ')}}</p>
                              <span class="cl-text" style="line-height: 20px;" v-if="pointOption.type3">{{pointOption.returnFeedback3.split(",").join('， ')}}</span>
                              <!-- <div class="content">
                                 <ul>
                                       <li :class="{textActive: num===0}" v-for="(item,num) in vo.courses" :key="num" class="cl-side-text">{{item.courseName}}</li>
                                 </ul>
                              </div> -->
                           </div>
                        </div>
                  </li>
               </ul>
            </div>
      </div>
      <div class="college_search">
            <div class="college_search_lf">
               <span class="cl-btn">指导记录</span>
            </div>
            <div class="college_search_rt">
               <el-select v-model="page.year" filterable placeholder="请选择学年" class="new-select" @change="changeyear">
                  <el-option v-for="item in yearOptions" :key="item" :label="`${item} - ${item + 1}学年`" :value="item"></el-option>
               </el-select>
               <el-select v-model="page.term" filterable placeholder="请选择学期" @change="changeterm">
                  <el-option v-for="item in termOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
               </el-select>
            </div>
      </div>
      <div class="college_tabel cl-tabel">
            <el-table :data="tableData" stripe style="width: 100%">
               <el-table-column prop="courseName" label="课程"></el-table-column>
               <el-table-column prop="teacherName" label="代课老师"></el-table-column>
               <el-table-column prop="grade" label="学年"></el-table-column>
               <el-table-column prop="term" label="学期"></el-table-column>
               <el-table-column prop="comment" label="指导内容"></el-table-column>
            </el-table>
            <div class="college_pagination">
               <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="page.page" :page-sizes="[15,30,40,50]" :page-size="page.rows" layout="prev, pager, next, jumper, sizes, total" :total="total"></el-pagination>
            </div>
      </div>
   </div>
</div>
</template>

<script>
import Self from "@/api/self";
import echarts5 from "@/components/echaars/echars5"
export default {
   name: 'CollegeStudent',
   components: {
      echarts5
   },
   data() {
      return {
            activeIndex: 0,
            activeIndexTwo: 0,
            topActiveIndex: 0,
            topActiveIndex1: 0,
            nameTips: '工程知识',
            seriesBarData: {},
            options: [],
            topOptions: [],
            topOptions1: [],
            yearOptions: [],
            termOptions: [{
                    name: '全部',
                    id: '0'
                },
                {
                    name: '第一学期',
                    id: '1'
                },
                {
                    name: '第二学期',
                    id: '2'
                }
            ],
            pointOption: {},
            tableData: [],
            total: 0,
            page: {
                year: '',
                term: '',
                page: 1,
                rows: 15
            },
            xuerowifon:{},
            avgRatioList:[],
            tremid:'',
            gradeifon:'',
            tremifon:'',
            getgrade:0,
            name: ''
      }
   },
   created() {
      this.initData()
      this.studentAnalyze()
      this.getgrade =  this.$route.query.row.grade
   },
   mounted() {
      // this.gettopOptions()
      // this.getportrayal()
         // versionId:this.versionId,
         //            specialtyId:this.$route.query.id,
      this.grades()
    },
    methods: {
      grades() {
         // let specialtyId = '627906732441468928'
         this.$get(`/basic/scheduling/grades/${this.$route.query.specialtyId}`).then(res => {
               if (res.status == 1) {
                  this.yearOptions = res.data
               }
         })
      },
      studentAnalyze(){
         this.$get(`/basic/specialty/portrayal/studentAnalyze?studentId=${this.$route.query.row.id}`).then(res=>{
            if(res.status == 1){
               this.xuerowifon = res.data.sutdent
               this.options = res.data.grademap.yearTerm
               this.topOptions = res.data.grademap.claims
               this.topOptions1 = res.data.grademap.claims
               this.gettopOptions(this.options[0].term, this.topOptions[0].id,this.$route.query.row.grade)
               setTimeout(()=>{
                  this.getportrayal(this.options[0].term,this.$route.query.row.grade)
               },400)
               
               this.gradeifon = this.options[0].grade + '-'+ Number(this.options[0].grade + 1) 
               this.tremifon = '一'
            }
         })
      },
      // 能力达成概况
      gettopOptions(a,b,grade){
         let grade1 = grade 
         this.$get(`/basic/specialty/portrayal/courseSocreByGraduationId/${b}/${grade1}/${a}/${this.$route.query.row.id}`).then(res=>{
            if(res.status === 1){
               const top = ["我的分值", "平均值", "目标值"];
               const tip = res.data.name.split(",").map(item => {
                  return `指标点${item}`;
               });
               const echartData = {
                  legendData: top,
                  xAxisData: tip,
                  seriesData: top.map(item => {
                     return {
                     name: item,
                     barGap: "0", //柱状图间距
                     data: []
                     };
                  })
               };
               echartData.seriesData[0].data = res.data.ratio.split(",");
               echartData.seriesData[1].data = res.data.avgRatioStr.split(",");
               echartData.seriesData[2].data = res.data.targetRatioStr.split(",");
               this.seriesBarData = echartData;
            }
         })
      },
      //   预警分析  /basic/specialty/portrayal/earlyWarning/627938814555324416/627948326993526784/2015/1/627940385603190784
      getportrayal(a,grade){
         // let graduationId = b || a 
         let grades = grade || this.$route.query.row.grade
         this.$get(`/basic/specialty/portrayal/earlyWarning/${this.$route.query.versionId}/${grades}/${a}/${this.$route.query.row.id}`).then(res=>{
            console.log(res,'能力预警')
               // res.data.avgRatioList.forEach((element,index) => {
                  // if(element.point_course_mark > element.ratioMark){
                  //       element.status1 = 0
                  // }else if(element.ratioMark < element.point_course_mark &&  element.point_course_mark < element.targetMark){
                  //       element.status1 = 1
                  // }else if(element.point_course_mark > element.targetMark){
                  //       element.status1 = 2
                  // }else if(element.ratioMark==0 && element.point_course_mark==0 &&  element.targetMark==0){
                  //       element.status1 = 2
                  // }
               // });
            this.pointOption = res.data
         })
      },
      //  学生指导记录  /basic/specialty/portrayal/studentCourse
      changeyear(){
         // this.initData()
         this.initData(this.page.year,this.page.term) 
      },
      changeterm(){
         this.initData(this.page.year,this.page.term) 
      },
      initData(a,b) {
         let key = {
            page:this.page.page,
            rows:this.page.rows,
            studentId:this.$route.query.row.id,
            grade:a,
            term:b
         }
         this.$post('/basic/specialty/portrayal/studentCourse',key).then(res=>{
            if(res.status == 1){
               this.tableData = res.data.list
               this.total = Number(res.data.total)
            }
         })
      },
      tabClick(index, status,vo) {
         if (status === 0) {
               this.activeIndex = index
               this.tremid = vo.term
               this.getgrade = vo.grade || this.$route.query.row.grade
               this.gettopOptions(vo.term,this.topOptions[0].id,this.getgrade)
               
         } else {
            this.getgrade = vo.grade || this.$route.query.row.grade
            this.getportrayal (vo.term,this.getgrade)
            
            if(vo.term == 2){
               this.gradeifon = vo.grade + '-'+ Number(vo.grade + 1) 
               this.tremifon = '二'
            }else if(vo.term == 1){
               this.gradeifon = vo.grade + '-'+ Number(vo.grade + 1)
               this.tremifon = '一'
            }
               this.activeIndexTwo = index
               this.tremid = vo.term
         }
      },
      topClick(vo, index) {
         this.gettopOptions(this.options[0].term,vo.id,this.getgrade)
         this.nameTips = vo.name
         this.topActiveIndex = index
      },
      // topClick1(vo, index) {
      //    this.getportrayal(this.options[0].term,vo.id,this.getgrade)
      //    this.nameTips = vo.name
      //    this.topActiveIndex1 = index
      // },
      handleSizeChange(val) {
         this.page.rows = val;
         this.initData();
      },
      handleCurrentChange(obj) {
         this.page.page = obj;
         this.initData();
      },
      goback() {
         window.history.back(-1)
      }
   }
}
</script>

<style lang="scss">
@import "../../styles/college.scss";
</style>



